  <template>
    <div id="result">
      <el-tag>招新报名表，共有{{tableData.length}}条数据</el-tag>
       <el-table
                :data="tableData"
                :default-sort = "{prop: 'profession', order: 'descending'}"
                stripe
                style="width: 99%">
            <el-table-column
                    v-for="column in tableProps"
                    :key="column.prop"
                    :prop="column.prop"
                    sortable
                    :label="column.label">
                <template slot-scope="scope">
                    <span v-if="column.prop == 'handle'">
                        <el-button type="primary" class="btn" plain @click="confirmDeleteDialogVisible=true;getInfo(scope.row)">详细信息</el-button>
                    </span>
                    <span v-else>{{scope.row[column.prop] || '---'}}</span>
                </template>
            </el-table-column>

        </el-table>

        <el-dialog
                title="详细信息"
                :visible.sync="confirmDeleteDialogVisible"
                width="80%">
            <el-form >
                <el-form-item
                       v-for="(key,index) in userDetail" :key="index" 
                       :label="index" :prop="index">
                    {{userDetail[index]}}
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="confirmDeleteDialogVisible = false;">确 定</el-button>
          </span>
        </el-dialog>
    </div>
  </template>
  
  <script>
import { config } from "../config/config";
// import { Table, TableColumn } from "element-ui";

export default {
  // components: {
  //   Table,
  //   TableColumn
  // },
  data() {
    return {
      tableData: [],
      confirmDeleteDialogVisible: false,
      userDetail: {},
      tableProps: [
        {
          label: "专业",
          prop: "profession"
        },
        {
          label: "学号",
          prop: "id"
        },

        {
          label: "name",
          prop: "name"
        },
        // {
        //   label: "introduce",
        //   prop: "introduce"
        // },
        {
          label: "是否参加宣讲会",
          prop: "isJoin"
        },
        {
          label: "加权",
          prop: "grade"
        },
        {
          label: "了解途径",
          prop: "known"
        },
        {
          label: "phone",
          prop: "phone"
        },

        {
          label: "操作",
          prop: "handle"
        }
      ]
    };
  },
  mounted() {
    this.$http
      .get(config.basicUrl + config.user_list)
      .then(res => {
        console.log(res);
        this.tableData = res.body;
      })
      .catch(err => {
        console.log("error result , reason => ", err);
      });
  },
  methods: {
    getInfo(row) {
      let id = row._id;
      let reqUrl = config.basicUrl + config.get_user;
      this.$http
        .get(reqUrl + "?phone=" + id)
        .then(res => {
          if (res.body.error) {
            this.$message.error("查询失败" + `${res.body.error.msg}`);
          } else {
            this.userDetail = res.body;
          }
        })
        .catch(err => {
          this.$message.error("查询失败");
        });
    }
  }
};
</script>
  
  <style>
</style>
  


